.contact textarea {
    font-size: 16px;
    width: 80%;
    min-height: 200px;
    margin: 20px 0;
    line-height: 1.5;
}

.contact button {
    display : block;
    margin : 0 20% 0 auto;
    border : 2px solid black;
    background-color: #fff;
    cursor: pointer;
    padding: 10px 20px;
    font-size: 20px; 
    transition: .2s;
}

.contact button:hover {
    color : yellow;
    background-color: #000;
}